.App {
  margin: 30px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.5);
  font-family: Rajdhani, sans-serif;
}

/*** HEADER COMPONENT ***/

header {
  height: 70px;
  width: 100%;
  background-color: #fff;
  border-bottom: 2px solid #999;
  display: flex;
}

.flag {
  width: 60px;
  height: 43px;
  margin: auto 1em;
}

.page_title {
  margin: auto 1em auto 0;
  padding: 0;
  color: #0066cc;
  font-size: 2em;
  font-weight: 700;
}

.page_title span {
  white-space:nowrap;
}

.button {
  margin: 0.4em 1em 0 auto;
  cursor: pointer;
  border-color: #fff;
  background-color: #fff;
}

/*** FOOTER ***/

.footer {
  position: absolute;
  right: 40px;
}

.footer p {
  color: #000;
  font-size: 0.8em;
}

/*** SIDEBAR ***/

.sidebar-wrapper {
  display: flex;
  background-color: #fff;
}

.sidebar {
  position: absolute;
  flex-basis: 300px;
  height: 100%;
  top: 102px;
  background-color: #f1f1f1;
  z-index: 1;
  transition: 0.3s;
  overflow-y: scroll;
}


/* SEARCH BAR */

.search-location-bar {
  padding: 20px 20px 0 20px;
}

.search-location-input {
  width: 100%;
  padding: 12px 25px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  height: 55px;
  font-size: 0.9em;
  font-family: Rajdhani, sans-serif;
}

.search-location-label {
  font-style: italic;
  font-size: 0.9em;
  position: absolute;
  top: 35px;
  left: 35px;
  transition: 0.3s;
}

.search-location-input:focus+.search-location-label {
  font-size: 0.7em;
  color: #bfbfbf;
  top: 32px;
  left: 30px;
}

/* SEARCH RESULTS - LIST OF LOCATIONS */

.location-list {
  padding-inline-start: 0;
}

.location-list-item {
  list-style-type: none;
  padding: 0 20px;
  font-size: 1.2em;
  cursor: pointer;
  transition: 0.3s;
  border-bottom: 1px solid #cccccc;
  border-top: 1px solid #cccccc;
  box-sizing: border-box;
}

.location-list-item:hover, .location-list-item:focus {
  background-color: #bfbfbf;
  padding-left: 50px;
}

.location-list-item p:first-of-type {
  margin-bottom: 0;
}

.location-list-item p:last-of-type {
  margin-top: 2px;
  font-style: italic;
  font-size: 0.8em;
}

.no-location-found {
  padding: 0 20px;
  font-size: 1.2em;
  color: #737373;
}

/*** INFO WINDOW ***/

.info-window {
  display: flex;
  flex-direction: column;
  font-family: Rajdhani, sans-serif;
  text-align: center;
  padding-left: 25px;
  width: 18em;
  height: 10em;
}

.info-window h3 {
  font-weight: 700;
  font-size: 1.3em;
  color: #0066cc;
  margin-bottom: 0;
}

.info-window p {
  font-style: italic;
  font-size: 1.2em;
  margin-top: 5px;
}

.info-window .details-button {
  font-family: Rajdhani, sans-serif;
  font-size: 1.25em;
  align-self: center;
  background-color: #cce6ff;
  width: 50%;
  border-radius: 5px;
  cursor: pointer;
}

/*** LOCATION DETAILS - MODAL ***/

.modal_open {
  display: block
}

.modal_closed {
  display: none
}

.modal {
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    background-color: #fefefe;
    margin: 5% auto;
    padding: 20px;
    border: 1px solid #888;
    max-width: 750px;
    flex-direction: column;
}

.location-name {
  font-family: Rajdhani, sans-serif;
  font-size: 1.25em;
  color: #0066cc;
  margin-top: 0;
}

.location-name span {
  font-style: italic;
  font-size: 0.9em;
}

/* PHOTOGALLERY */

.photogallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 0;
  list-style-type: none;
}

.photogallery li {
  margin: 0 0 1em 0;
  width: 170px;
  height: 120px;
  background-color: #e6e6e6;
  box-sizing: border-box;
}

.photogallery .photo {
  background-size: cover;
  width: 100%;
  height: 100%;
}

.flickr  {
  display: flex;
  justify-content: space-between;
}

.flickr span {
  font-size: 0.9em;
  font-weight: 700;
  color: #0066cc
}

.flickr a {
  font-size: 0.9em;
  color: #0066cc;
}

/* WIKIPEDIA */

.location-info {
  border-top: 1px solid #737373;
  border-bottom: 1px solid #737373;
  background-color: #f1f1f1;
  max-height: 250px;
  text-align: justify;
  overflow-y: auto;
}

.wikipedia  {
  display: flex;
  justify-content: space-between;
}

.wikipedia .powered-by {
  font-size: 0.9em;
  font-weight: 700;
  color: #0066cc
}
.wikipedia .no-wiki-data {
  font-size: 0.9em;
}

.wikipedia a {
  font-size: 0.9em;
  color: #0066cc;
}

.no-wiki-data span {
  font-weight: 700;
  color: #0066cc
}

/* CLOSE button */

.close {
    width: 100%;
    color: #aaa;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    margin: -10px 5px 0 0;
    text-align: right;
}

.close:hover,
.close:focus {
    color: black;
    cursor: pointer;
}

/* ====================== MEDIA QUERIES ====================== */

@media screen and (max-width: 800px) {
  .photogallery {
    justify-content: space-around;
  }
}

@media screen and (max-width: 630px) {
  .App {
    margin: 15px;
  }

  .page_title {
    font-size: 1.2em;
  }

  .info-window {
    width: 13em;
    height: 7em;
  }

  .info-window h3, .info-window .details-button {
    font-size: 1em;
  }

  .info-window p {
    font-size: 0.9em;
  }

  .sidebar {
    height: 100%;
    width: 200px;
    top: 87px;
  }

  .search-location-bar {
    padding: 0;
  }

  .search-location-label {
    font-size: 0.7em;
    top: 15px;
    left: 10px;
  }

  .search-location-input:focus+.search-location-label {
    font-size: 0.5em;
    top: 15px;
    left: 10px;
  }

  .location-list {
    overflow-y: scroll;
  }

  .location-list-item {
    font-size: 0.9em;
  }

  .location-list-item:hover {
    padding-left: 35px;
  }

  .location-list-item p:last-of-type {
    font-size: 0.7em;
  }
}

@media screen and (max-width: 450px) {
  .photogallery li {
    width: 85px;
    height: 60px;
  }

  .location-name {
    font-size: 1em;
  }

  .location-info {
    font-size: 0.9em;
  }

  .flickr span, .flickr a, .wikipedia a, .wikipedia .powered-by, .wikipedia .no-wiki-data {
    font-size: 0.8em;
  }

  .close {
      font-size: 25px;
  }
}

@media screen and (max-width: 380px) {
  .App {
    margin: 0;
  }

  .page_title {
    font-size: 1em;
  }
}
